<template>
  <div id="test">
    <form>
      Wybierz miasto<br />

      <select v-model="City" @change="getData()" id="standard-select">
        <option v-for="city in Cities" v-bind:key="city">{{
          city.City
        }}</option>
      </select>
    </form>
    <br />

    <h1>{{ City }}</h1>
    <div class="center flexBlock">
      <table v-show="showChart">
        <thead>
          <tr></tr>
          <th>Data</th>
          <th>Temperatura</th>
          <th>Opady</th>
          <th>Wiatr</th>

          <th></th>
          <th></th>
        </thead>
        <tbody id="show">
          <tr v-for="(element, index) in info" v-bind:key="index">
            <td>{{ element.Date }}</td>
            <td>{{ element.Temperature }} °C</td>
            <td>{{ element.Rain }} mm / 12h</td>
            <td>{{ element.Wind }} km/h</td>
          </tr>
        </tbody>
      </table>
    </div>
    <br>
    <h1 v-show="showChart"> Temperatura</h1>
    <line-chart v-show="showChart" :data="chartData"></line-chart>
  </div>
</template>
<script>
var showChart = new Boolean();

import axios from "axios";
import { ApiKey1 } from "../main";
//import CitiesJson from '../components/Cities.json';
//import Test from '../components/long.json';
export default {
  data() {
    return {
      error: null,
      info: [],
      Cities: [],
      City: "",
      dataentry: null,
      datalabel: null,
      chartData: {},
      charLabel: [],
      showChart,
      test: [],
    };
  },
  methods: {
    getData() {
      

      axios
        .get(
          "https://my.api.mockaroo.com/LongTerm/" +
            this.City +
            ".json?key=" +
            ApiKey1
        )
        .then((response) => {
          this.info = response.data;
          var obj1 = '{ "';
          response.data.forEach((element) => {
            obj1 += element.Date + '" : ' + element.Temperature + ', "';
            this.charLabel = this.charLabel.concat(element.Date);
          });
          obj1 = obj1.slice(0, -3);
          obj1 += "}";
          console.log(obj1);
          this.chartData = JSON.parse(obj1);
        });

      this.showChart = true;
    },
    getCities() {
      axios
        .get("https://my.api.mockaroo.com/cities.json?key=" + ApiKey1)
        .then((response) => (this.Cities = response.data));
    },
  },
  beforeMount() {
    this.getCities();
    this.showChart = false;
  },
};
</script>
<style scoped>
.center {
  margin: 0 auto;
  max-width: 1200px;
  padding: 0 30px;
  margin-bottom: 50px;
}
.flexBlock {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
table {
  width: 1000px;
  margin: 0 auto;
}
th,
td {
  padding: 20px;
}
table {
  border-collapse: collapse;
}
tr {
  border-bottom: 1pt solid #15202b;
}
</style>
